<template>
  <div class="myuser">
    <div class="pageTop">
      <div class="wraprl">
        <div class="pageHead flexcenter cfff fw500">
          
          <div v-if="this.$store.state.web3.isInjected==true" class="uploadIcon flexcenter iconfont icon-ziyuan cfff"></div>
          <div v-else class="uploadIcon flexcenter ">
            <img src="../../../static/eth_plug.png" @click="installMetaMask()"  >
          </div>
        </div>
        <div class="progress">
          <div class="msg tl cfff">
            
            <span>{{$store.state.gameInfo.pid}}</span>
            <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
            
            <span>{{$store.state.etherNetUtilInfo.etDifficulty}}</span> EP
          </div>
          <dappprogress class="wrap"></dappprogress>
        </div>
      </div>
    </div>
    <div class="main wraprl">
      <div class="accountInfo">
        <div class="ultab ultab1 flex pre">
          <div class="tab tab1 flex1 flexcenter" :class="checkTab=='tab1'?'active':''" @click="checkTab='tab1'"></div>
          <div class="tab tab2 flex1 flexcenter" :class="checkTab=='tab2'?'active':''" @click="checkTab='tab2'"></div>
        </div>
        <div class="con bgfff" :class="checkTab=='tab1'?'Radiustopright':'Radiustopleft'">
          <div v-if="checkTab=='tab1'">
            <div class="flex flexjcsb title">
              <span class="coinbase textWrap">{{ this.coinbase }}</span>
              <div class="dengji pre cfff">
                <img src="../../../static/dengji.png" />
                <span class="num">{{ $store.state.player.level }}</span>
                <span class="text">{{ $store.state.player.levelName }}</span>
              </div>
            </div>
            <div class="flex flexjcsb li li1">
              <div class="coinName">
                <img src="../../../static/yitaifang.png" />
                :
              </div>
              <div class="unit">
                <span>{{this.balance | numFilter}}</span> ETH /
                <span>{{this.balance * this.$store.state.priceInfo.ethPrice   | numFilter}}</span>CNY
              </div>
            </div>
            <div class="flex flexjcsb li li1">
              <div class="coinName">
                <img src="../../../static/taidabi.png" />
                :
              </div>
              <div class="unit">
                <span>{{this.$store.state.balanceInfo.usdtBalance | numFilter}}</span> USDT /
                <span>{{this.$store.state.balanceInfo.usdtBalance * this.$store.state.priceInfo.usdtPrice | numFilter}}</span>CNY
              </div>
            </div>
          </div>
          <div v-if="checkTab=='tab2'">
            <div class="flex flexjcsb title">
              <span class="op0"></span>
              <div class="dengji pre cfff">
                <img class="shengjiImg" @click="showWindow('shengji')" src="../../../static/shengji.png" />
                <img class="fenxiangImg" @click="showWindow('fenxiang')" src="../../../static/fenxiang.png" />
              </div>
            </div>
            <div class="flex flexjcsb li li1">
              <div class="coinName">
                <img src="../../../static/ets.png" />
                :
              </div>
              <div class="unit">
                <span>{{ parseFloat($store.state.player.etsFreeAmount) | numFilter}} </span> ETS /
                <span>{{ parseFloat($store.state.player.etsFreeAmount) * $store.state.priceInfo.etsPrice | numFilter }}</span>CNY
              </div>
            </div>
            <div class="flex flexjcsb li li1">
              <div class="coinName">
                <img src="../../../static/et.png" />
                :
              </div>
              <div class="unit">
                <span>{{ parseFloat($store.state.player.etFreeAmount)  | numFilter}}</span> ET /
                <span>{{ parseFloat($store.state.player.etFreeAmount) * $store.state.priceInfo.etPrice | numFilter }}</span>CNY
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="menulist">
        <div class="li bgfff flex flexac">
          <div class="grow">
            <div class="row row1 flex flexjcsb">
              <div class="flex flexac">
                <img class="coinlogo" src="../../../static/gongzhenchi.png" />
                <span class="tabname"></span>
                <span class="spot">|</span>
                <span class="num">{{$store.state.player.etsFreezeAmount | numFilter}}</span>ETS
              </div>
            </div>
            <div class="row row2 flex flexjcsb">
              <div>
                :
                <span>{{$store.state.player.etsFreeAmount}}</span> ETS
                <img src="../../../static/ti.png" @click="withDrawETS" />
              </div>
            </div>
          </div>
          <div class="Boxright flex flexdc">
            <span class="caozuoBtn caozuoBtn1" @click="showWindow('gongzheng')"></span>
            <span class="caozuoBtn caozuoBtn2" @click="showWindow('pingxing')"></span>
          </div>
        </div>
        <div class="li bgfff flex flexac">
          <div class="grow">
            <div class="row row1 flex flexjcsb">
              <div class="flex flexac">
                <img class="coinlogo" src="../../../static/kuangvhisuanli.png" />
                <span class="tabname"></span>
                <span class="spot">|</span>
                <span class="num">{{$store.state.player.etFreezeAmount}}</span>ET
              </div>
            </div>
            <div class="row row2 flex flexjcsb">
              <div>
                :
                <span>{{$store.state.player.etFreeAmount}}</span> ET
                <img src="../../../static/ti.png" @click="withDrawET" />
              </div>
            </div>
          </div>
          <div class="Boxright flex flexdc">
            <span class="caozuoBtn caozuoBtn1" @click="showWindow('zhurusuanli')"></span>
          </div>
        </div>
        <div class="li bgfff flex flexac">
          <div class="grow">
            <div class="row row1 flex flexjcsb">
              <div class="flex flexac">
                <img class="coinlogo" src="../../../static/jilian.png" />
                <span class="tabname"></span>
                <span class="spot">|</span>
                <span> &nbsp;5</span><span class="spot">/</span><span> &nbsp;5654</span>
              </div>
            </div>
            <div class="row row2 flex flexjcsb">
              <div>
                : ()<span>1000ET</span> ()<span>1000ET</span>
              </div>
            </div>
          </div>
          <div class="Boxright flex flexdc">
            <!-- <span class="caozuoBtn caozuoBtn1"></span> -->
          </div>
        </div>
        <div class="li bgfff flex flexac">
          <div class="grow">
            <div class="row row1 flex flexjcsb">
              <div class="flex flexac">
                <img class="coinlogo" src="../../../static/gongxianli.png" />
                <span class="tabname"></span>
                <span class="spot">|</span>
                <span class="num">1000</span>ET
              </div>
            </div>
            <div class="row row2 flex flexjcsb">
              <div>
                :
                <span>1000ET</span>CNY
              </div>
            </div>
          </div>
          <div class="Boxright flex flexac fanlilv">
            :<span>21%</span>
          </div>
        </div>
      </div>
    </div>
    <!-- @click="closeWindow" -->
    <div v-if="maskwindow" class="maskwindow" @touchmove.prevent></div>
    <div v-if="popuptype=='fenxiang'" class="popupWindow fenxiangWindow">
      <div class="url linewrap"  v-if="$store.state.player.inviteUrl==null||$store.state.player.inviteUrl==''">  {{$store.state.player.inviteUrl}} </div>
      <div class="url linewrap"  v-else >{{$store.state.player.inviteUrl}}</div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeWindow"></div>
        <div class="copyCodeText define flexcenter submitBtn" :data-clipboard-text="`${$store.state.player.inviteUrl}`"     @click="copyCode()"></div>
      </div>
    </div>
    <div v-if="popuptype=='gongzheng'" class="popupWindow gongzhengWindow" @click="closepicker">
      <div class="title">ETHUSDT</div>
      <div class="cointype">
        <div class="info"></div>
         <div class="writeInput flex flexac ">
          <input style="width:20%;" class="grow"  placeholder="" type="number" step="0.01" v-model="powerNubmer" @blur="powerNubmerChanageCoin" />
          <div style="width:80px;padding-left:10px;"></div>
        </div>
        <div class="writeInput flex flexac ">
          <input style="width:20%;" class="grow"  placeholder="ETS" type="number" step="0.01" disabled  v-model="powerETSNubmer"  />
          <div style="width:80px;padding-left:10px;"> ETS</div>
        </div>
        <div class="writeInput selectList  flex flexac">
          <div class="pre grow" style="width:60%;">
              <input type="text" v-model="resonanPowerCoin" placeholder="ETH" @click="showSelectCoin" />
              <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
        <!--
        <div class="info pwd"></div>
        <div class="writeInput">
          <input type="password" placeholder="" />
        </div>
        -->
      </div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeResonanPower"></div>
        <div class="define flexcenter submitBtn" @click="resonanPower"></div>
      </div>
    </div>
    <div v-if="popuptype=='pingxing'" class="popupWindow gongzhengWindow pingxinggongzheng">
      <div class="title">21ETH 1:1 ETS</div>
      <div class="cointype"> 
        <div class="con">  
          <div v-if="this.$store.state.player.parallelDay > 0"  class="flex flextc text"></div>
          <div v-if="this.$store.state.player.parallelDay > 0"  class="flex flextc day">{{this.$store.state.player.parallelDay}}  </div>
          <div v-if="this.$store.state.player.parallelDay <= 0" class="flex flextc text"> {{this.balance | numFilter}}  ETS  </div>
        </div>
        <!--
        <div class="info pwd flex"></div>
        <div class="writeInput">
          <input style="width:90%" type="password" placeholder="" />
        </div>
        -->
      </div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeResonanParallel"></div>
        <div class="define flexcenter submitBtn" @click="resonanParallel"></div>
      </div>
    </div>
    <div v-if="selectCoin" class="selectCoin">
      <div class="defineCancel flex flexjcsb">
        <div class="cancel flexcenter" @click="closeSelectCoin"></div>
        <div class="define flexcenter" @click="sureSelectCoin"></div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </div>
   <div v-if="popuptype=='shengji'" class="popupWindow shengjiWindow">
      <div class="title">
        <div class="text"></div>
        <div class="num">{{this.$store.state.player.etSeedAmount}} ET</div>
      </div>
      <div class="con">
        <div class="info"> 
          
          <span>{{this.$store.state.player.levelName}}</span>,  "{{this.$store.state.player.upLevelName}}" ,  "" ET, , !
        </div>
        <div class="msg">
          
          <span class="nodeType">{{this.$store.state.player.upLevelName}}</span>
          <span class="num">{{this.$store.state.player.upLevelNubmer}} ET</span>
        </div>
      </div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeBuyLevel"></div>
        <div class="define flexcenter submitBtn" @click="buyLevel"  ></div>
      </div>
    </div>
    <div v-if="popuptype=='zhurusuanli'" class="popupWindow shengjiWindow">
      <div class="title">
        <div class="text">ET</div>
      </div>
      <div class="con">
        <div class="info">
           {{$store.state.etherNetUtilInfo.etDifficulty}} EP
        </div>
        <br>
        <br>
        <div class="writeInput flex flexac "> 
          <input style="width:60%;" class="grow" type="text" placeholder="ET"  v-model="etPoolNubmer"/>
          <div style="width:80px;padding-left:10px;"> ET</div>
        </div>
      </div>
      <div class="btnul flex flexjcsb">
        <div class="cancel flexcenter" @click="closeEtPool"></div>
        <div class="define flexcenter submitBtn" @click="etPool"></div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { ethers } from "ethers";
import Clipboard from "clipboard";
import { NETWORKS } from "../../factory/contract/Networks.js";
import { MessageBox } from "mint-ui";
import { Toast } from 'mint-ui';
import Providers from "../../factory/contract/Providers.js";
import Constants from "../../factory/contract/Constants.js";
const random = require("string-random");
const axios = require("axios");
import progresscomponent from "../../components/progress";
export default {
  data() {
     return {
      checkTab: "tab1",
      popuptype:'',
      maskwindow: false,
      selectCoin: false,
      selectChangeCoin: "",
      resonanPowerCoin: "ETH",
      etPoolNubmer: "",
      powerETSNubmer: "",
      powerNubmer: "",
      slots: [
        {
          flex: 1,
          values: [
            "ETH",
            "USDT",
          ],
          className: "slot1",
          textAlign: "right"
        }
      ],
    };
  },
  computed: {
    ...mapState({
      network: state => NETWORKS[state.web3.networkId],
      coinbase: state => state.web3.coinbase,
      balance: state => state.web3.balance
    })
  },
  async created() {
    this.$store.state.player.beInvitedCode =  this.$route.query.code; 
    console.log("created  console " + this.$store.state.player.beInvitedCode);
  },
  beforeDestroy() {},
  async mounted() {
    console.log("mounted  ");
    if (Providers.isMetaMaskProvider()) {
      console.log("isMetaMaskProvider  ");
    }
    console.log("mounted  console ");
  },
  filters: {
    numFilter(value) {
      let realVal = "";
      if (value) {
        realVal = parseFloat(value).toFixed(2);
      } else {
        realVal = "0.00";
      }
      return realVal;
    },
    formatDate(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      return y + "-" + MM + "-" + d;
    }
  },
  methods: {
    powerNubmerChanageCoin: function() {
      if(this.resonanPowerCoin=="ETH"){
        this.powerETSNubmer = this.powerNubmer * this.$store.state.priceInfo.ethPrice/this.$store.state.priceInfo.etsPrice;
        this.powerNubmer = this.glabal.toDecimal(this.powerNubmer);
        this.powerETSNubmer = this.glabal.toDecimal(this.powerETSNubmer * this.$store.state.etherNetUtilInfo.inflationRate);
        console.log();
        console.log(this.powerNubmer);
        console.log(this.$store.state.etherNetUtilInfo.inflationRate);
        console.log(this.powerETSNubmer* this.$store.state.etherNetUtilInfo.inflationRate);
      }else{
        this.powerETSNubmer = this.powerNubmer * this.$store.state.priceInfo.usdtPrice/this.$store.state.priceInfo.etsPrice;
        this.powerNubmer = this.glabal.toDecimal(this.powerNubmer);
        this.powerETSNubmer = this.glabal.toDecimal(this.powerETSNubmer * this.$store.state.etherNetUtilInfo.inflationRate);
        console.log(this.powerETSNubmer);
        console.log(this.powerNubmer);
        console.log(this.$store.state.etherNetUtilInfo.inflationRate);
      }
    },
    closeResonanParallel() {
      this.maskwindow = false;
      this.popuptype = '';
    },
    resonanParallel() {
      let _this = this;
      if (_this.coinbase == null||_this.$store.state.player.regTime==0) return;
      console.log(" resonanParallel  ");
      this.$store.state
        .etherNetContractInstance()
        .resonanParallel()
        .then(tx => {
          console.log(" resonanParallel  " + tx);
          Toast("! ID:" + tx.hash);
          this.closeResonanParallel();
        });
    },
    closeResonanPower(){
      this.powerETSNubmer = "";
      this.powerNubmer = "";
      this.maskwindow = false;
      this.popuptype = '';
    },
    resonanPower() {
      if(this.resonanPowerCoin=="ETH"){
        this.resonanPowerEth();
      }else{
         this.resonanPowerUsdt();
      }
      
    },
    resonanPowerEth() {
      console.log(" resonanPowerEth  " );
      let _this = this;
      if (_this.coinbase == null||_this.$store.state.player.regTime==0) return;
      if(this.glabal.isNull(this.powerNubmer)||parseFloat(this.powerNubmer)<=0){
         Toast('');
         return;
      }
      if(parseFloat(_this.balance) < parseFloat(this.powerNubmer)){
         Toast('ETH');
         return;
      }
      let overrides = {
        value: ethers.utils.parseEther("" + this.powerNubmer)
      };
      _this.$store.state
        .etherNetCoreContractInstance()
        .resonanPowerEth(overrides)
        .then(tx => {
          Toast("! ID:" + tx.hash);
          this.closeResonanPower();
        });
    },
    resonanPowerUsdt() {
      console.log(" resonanPowerUsdt  ");
      let _this = this;
      if (_this.coinbase == null||_this.$store.state.player.regTime==0) return;
      if(this.glabal.isNull(this.powerNubmer)||parseFloat(this.powerNubmer)<=0){
         Toast('');
         return;
      }
      if(this.glabal.isNull(_this.$store.state.balanceInfo.usdtBalance) || parseFloat(_this.$store.state.balanceInfo.usdtBalance) < parseFloat(this.powerNubmer)){
         Toast('USDT');
         return;
      }
      this.$store.state
        .usdtContractInstance()
        .allowance(this.coinbase,Constants.EtherNetCoreContractAddress)
	      .then(remaining => {
          console.log(" resonanPowerUsdt  " + ethers.utils.parseUnits(_this.powerNubmer,"Mwei"));
          if(ethers.utils.formatUnits(""+remaining,"Mwei")>=parseFloat(this.powerNubmer) ){
            this.$store.state
            .etherNetCoreContractInstance()
            .resonanPowerUsdt(ethers.utils.parseUnits(_this.powerNubmer,"Mwei"))
            .then(tx => {
                Toast("! ID:" + tx.hash);
                this.closeResonanPower();
            });
          }else{
            MessageBox({
              title: "USDT",
              message: "USDT Token?",
              showCancelButton: true,
              confirmButtonText: "",
              cancelButtonText: ""
            })
            .then(action => {
                if (action == "confirm") {
                  this.$store.state
                    .usdtContractInstance()
                    .approve(Constants.EtherNetCoreContractAddress,ethers.utils.parseUnits("1000000","Mwei"))
                    .then(tx => {
                      Toast("! ID:" + tx.hash);
                    });
                }
            })
            .catch(() => {});
          }
      });
    },
    closeEtPool(){
      this.etPoolNubmer  = "";
      this.maskwindow = false;
      this.popuptype = '';
    },  
    etPool() {
      let _this = this;
      console.log(" etPool  " + this.etPoolNubmer );
      if (_this.coinbase == null||_this.$store.state.player.regTime==0) return;
      if(this.glabal.isNull(this.etPoolNubmer) || parseFloat(this.etPoolNubmer)<=0){
         Toast('');
         return;
      }
      if(this.glabal.isNull(this.etPoolNubmer)  || parseFloat(this.etPoolNubmer)>=parseFloat(_this.$store.state.player.etFreeAmount)){
         Toast('ET');
         return;
      }
      if(this.glabal.isNull(this.etPoolNubmer) || (parseFloat(_this.etPoolNubmer) % _this.$store.state.etherNetUtilInfo.etDifficulty)!=0){
         Toast('ET');
         return;
      }
      console.log(" etPool  ");
      _this.$store.state
        .etherNetContractInstance()
        .etPool(ethers.utils.parseUnits(_this.etPoolNubmer,"Mwei"))
        .then(tx => {
           console.log(" etPool  " + tx);
           Toast("! ID:" + tx.hash);  
           this.closeEtPool();
        });
    },
    closeBuyLevel(){
      this.maskwindow = false;
      this.popuptype = '';
    },  
    buyLevel() {
      console.log(" buyLevel  ");
      let _this = this;
      if (_this.coinbase == null||_this.$store.state.player.regTime==0) return;
      if(this.glabal.isNull(_this.$store.state.player.upLevelNubmer)||parseFloat(_this.$store.state.player.upLevelNubmer)>parseFloat(_this.$store.state.player.etFreeAmount)){
         Toast('ET');
         return;
      }
      _this.$store.state
        .etherNetContractInstance()
        .buyLevel()
        .then(tx => {
          console.log(" buyLevel  " + tx);
          Toast("! ID:" + tx.hash); 
          this.closeBuyLevel();
        });
    },
    isJoinGame() {
      let _this = this;
      MessageBox({
        $type: 'prompt',
        title: "",
        message: "<br>",
        confirmButtonText: "",
        cancelButtonText: "",
        closeOnClickModal: false,   
        showCancelButton: true,    
        inputErrorMessage: "",
        showInput: false
      }).then(action => {
        if (action === 'confirm') {
          console.log("confirm  console " + this.$store.state.player.beInvitedCode);
          if (this.glabal.isNull(_this.$store.state.player.beInvitedCode)) {
            Toast('');
            return;
          }
          let inviteCode = random(6);
          console.log("confirm  inviteCode " + inviteCode);
          console.log("confirm  beInvitedCode " + _this.$store.state.player.beInvitedCode);
          _this.$store.state
          .etherNetContractInstance()
          .joinGame(inviteCode,_this.$store.state.player.beInvitedCode)
          .then(info => {
            console.log(" joinGame  " + info);
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    onValuesChange(picker, values) {
      this.selectChangeCoin = values[0];
      console.log(" onValuesChange  +++++++" + values);
      console.log(" onValuesChange  +++++++ resonanPower_coin" + this.resonanPowerCoin);
    },
    withDrawETS: function () {
      this.isJoinGame();
      let _this = this;
      MessageBox({
        $type: 'prompt',
        title: "ETS",
        message: "ETS",
        confirmButtonText: "",
        cancelButtonText: "",
        closeOnClickModal: false,   
        showCancelButton: true,    
        inputValidator: function (v) { return /^((\d+\.\d{0,4})|(\d+))$/.test(v); },  //function
        inputErrorMessage: "",
        showInput: true
      }).then(({ value, action }) => {
        console.log(" value  "  +  value);
        console.log(" action  "  +  action);
        if (action == "confirm") {
          if (this.glabal.isNull(value)) {
            Toast('');
            return;
          }
          if (parseFloat(value) > parseFloat( _this.$store.state.player.etsFreeAmount)) {
            Toast('');
            return;
          }
          _this.$store.state
            .etherNetCoreContractInstance()
            .withDraw(ethers.utils.parseUnits(value,"Mwei"),20)
            .then(tx => {
              Toast("! ID:" + tx.hash);
          });
        }
      }).catch((e) => {  console.log(e); });
    },
    withDrawET: function () {
      this.isJoinGame();
      let _this = this;
      MessageBox({
        $type: 'prompt',
        title: "ET",
        message: "ET",
        confirmButtonText: "",
        cancelButtonText: "",
        closeOnClickModal: false,   
        showCancelButton: true,    
        inputValidator: function (v) { return /^((\d+\.\d{0,4})|(\d+))$/.test(v); },  //function
        inputErrorMessage: "",
        showInput: true
      }).then(({ value, action }) => {
        console.log(" value  "  +  value);
        console.log(" action  "  +  action);
        if (action == "confirm") {
          
          console.log(" isNull mark 1 "  );
          if (_this.glabal.isNull(value)) {
            Toast('');
            return;
          }

          console.log(" isNull mark 2 "  );
          if (parseFloat(value) > parseFloat( _this.$store.state.player.etFreeAmount)) {
            Toast('');
            return;
          }

          console.log(" isNull mark 3 "  );
          _this.$store.state
            .etherNetCoreContractInstance()
            .withDraw(ethers.utils.parseUnits(value,"Mwei"),30)
            .then(tx => {
              Toast("! ID:" + tx.hash);
          });
          console.log(" isNull mark 4 "  );
       }
       console.log(" gggggggggggggggggggggggggggggggggggggg "  );
      }).catch((e) => {  console.log(e); });
    },
    copyCode: function() {
      let _this = this;
      if (
        this.$store.state.player.inviteCode == null ||
        this.$store.state.player.inviteCode == "" ||
        this.$store.state.player.inviteUrl == null ||
        this.$store.state.player.inviteUrl == ""
      ) {
        Toast('');
        return;
      }
      let clip = new Clipboard(".copyCodeText");
      clip.on("success", function() {
        Toast('');
      });
      clip.on("error", function() {
        Toast('');
      });
    },
    showWindow(type) {
      let _this = this;
      console.log(" isJoinGame  " + _this.glabal.isJoinGame(_this.$store.state.player));
      if(_this.glabal.isJoinGame(_this.$store.state.player)==false){
         this.isJoinGame();
        return;
      }
      this.maskwindow = true;
      this.popuptype=type;
      console.log(" showWindow  " + type);
    },
    closeWindow() {
      this.maskwindow = false;
      this.popuptype = '';
      // this.fenxiang = false;
      // this.shengji = false;
      // this.gongzheng = false;
    },
    closepicker() {
      this.selectCoin = false;
    },
    showSelectCoin() {
      event.stopPropagation();
      this.selectCoin = true;
    },
    closeSelectCoin() {
      this.powerETSNubmer = "";
      this.powerNubmer = "";
      event.stopPropagation();
      this.selectCoin = false;
    },
    sureSelectCoin() {
      this.selectCoin = false;
      this.resonanPowerCoin = this.selectChangeCoin;
      console.log("sureSelectCoin " +  this.resonanPowerCoin);
      if(this.resonanPowerCoin=="ETH"){
        if(this.glabal.isNull(this.$store.state.priceInfo.ethPrice)||parseFloat(this.$store.state.priceInfo.ethPrice)<=0||
          this.glabal.isNull(this.$store.state.priceInfo.etsPrice)||parseFloat(this.$store.state.priceInfo.etsPrice)<=0||
          this.glabal.isNull(this.powerNubmer)||parseFloat(this.powerNubmer)<=0){
          return;
        }
        this.powerETSNubmer = this.powerNubmer * this.$store.state.priceInfo.ethPrice/this.$store.state.priceInfo.etsPrice;
        this.powerETSNubmer = this.glabal.toDecimal(this.powerETSNubmer * this.$store.state.etherNetUtilInfo.inflationRate);
        console.log(this.powerETSNubmer);
        console.log(this.powerNubmer);
      }else{
        if(this.glabal.isNull(this.$store.state.priceInfo.usdtPrice)||parseFloat(this.$store.state.priceInfo.usdtPrice)<=0||
          this.glabal.isNull(this.$store.state.priceInfo.etsPrice)||parseFloat(this.$store.state.priceInfo.etsPrice)<=0||
          this.glabal.isNull(this.powerNubmer)||parseFloat(this.powerNubmer)<=0){
          return;
        }
        this.powerETSNubmer = this.powerNubmer * this.$store.state.priceInfo.usdtPrice/this.$store.state.priceInfo.etsPrice;
        this.powerETSNubmer = this.glabal.toDecimal(this.powerETSNubmer * this.$store.state.etherNetUtilInfo.inflationRate);
        console.log(this.powerETSNubmer);
        console.log(this.powerNubmer);
      }
    },
    isEmpty(obj) {
      if (typeof obj == "undefined" || obj == null || obj == "") {
        return true;
      } else {
        return false;
      }
    },
    installMetaMask() {
      let _this = this;
      _this.glabal.installMetaMask();
    },
  },
  components: {
    dappprogress: progresscomponent
  }
};
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
@import "../../styles/my.scss";
.active .sanjiao1 {
  border-bottom: 23px solid #ffffff !important;
  border-left: 13px solid #ffffff !important;
}
.accountInfo {
  border-top: none;

  .ultab1 {
    height: 42px;
    .tab {
      height: 100%;
      position: absolute;
      z-index: 1;
      background-color: #ffe3d7;
      color: #333;
      width: 50%;
      height: 85%;
    }
    .tab1 {
      bottom: 0;
      left: 10px;
      border-top-left-radius: 8px;
      &.active {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background: url("../../../static/tab1.png");
        background-size: 100% 100%;
        left: 0px;
        width: 55%;
        height: 100%;
        z-index: 2;
        color: #000;
        font-weight: 600;
      }
    }

    .tab2 {
      bottom: 0;
      right: 10px;
      border-top-right-radius: 8px;
      &.active {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background: url("../../../static/tab2.png");
        background-size: 100% 100%;
        right: 0px;
        width: 55%;
        height: 100%;
        z-index: 2;
        font-weight: 600;
      }
    }
  }
  .con {
    width: 100%;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    padding: 20px;
    box-shadow: 2px #cccccc;
    .dengji {
      height: 20px;
      .shengjiImg,
      .fenxiang {
        width: 18px;
      }
      .shengjiImg {
        margin-right: 10px;
      }
      .fenxiang {
      }
      .num,
      .text {
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
      }
      .num {
        left: 10px;
      }
      .text {
        left: 0;
        right: 0;
        margin-left: 14px;
      }
    }
    .title {
      padding-bottom: 10px;
    }
    .li {
      padding: 10px 0;
    }
  }
}
.Radiustopright {
  border-top-right-radius: 8px;
}
.Radiustopleft {
  border-top-left-radius: 8px;
}
.menulist {
  margin-top: 25px;
  .li {
    margin-top: 15px;
    padding: 15px 10px;
    border-radius: 4px;
    .spot {
      padding: 0 5px;
    }
    .fanlilv {
      margin-top: 18px;
    }
    .row2 {
      margin-top: 10px;
    }
  }
}
</style>


